body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --primaryColor: #2892D7;
}

section{
    padding-bottom: 80px;
    padding-top: 80px;
}

.bg-smoke {
    background-color: whitesmoke;
}

.bg-primary {
    background-color: #2892D7;
    background-color: var(--primaryColor);
    color: white;
}
.titulo-about {
    font-size: 20px;
    margin-top: 45px;
    text-transform: uppercase;
}

.description-about {
    text-align: justify;
    line-height: 2.3rem;
    color: #CCFFFFFF;
}
  
.margin_right {
    margin-right: 10px;
}
.sobre_mim {
  padding-left: 32px;
  padding-right: 32px;
  margin-top: 55px;
  margin-bottom: 22px;
  font-size: 16px;
  text-align: justify;
}

.nome {
  font-weight: 600;
  text-transform: uppercase;
  color: black;
}

.titulo {
  font-size: 20px;
  margin-top: 45px;
  text-transform: uppercase;
  color: #2892D7;
}

.img-fluid {
    width: 80%;
    height: auto;
}
.padding-0{
    padding-right:0px;
    padding-left:0px;
}    

.padding-0 img {
    padding: 30px;
}

.padding-0 img:hover {
    padding: 15px;
}
.my-skills {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 32px;
}

.my-skills > li {
    list-style: none;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin: 4px;
    text-transform: uppercase;
    color: white;
    background-color: var(--primaryColor);  
    border: solid 1px var(--primaryColor);
}

.my-skills > li:hover{
    color: var(--primaryColor);
    background-color: white; 
    cursor: default; 
}
footer {
    height: 170px;
    background-color: #2892D7;
    display: flex;
    align-items: center;
}

.footer-info {
    color: white;
    padding: 1px;
    margin: 0px;
}

.social-container {
    padding-bottom: 5px;
}

.social-icon {
    margin: 10px;
}



nav.navbar, .navbar-default {
  background-color: #2892D7 !important;
}

.navbar-default {
  background-image: none;
  border: none;
  border-radius: 0;
}

.navbar-default .navbar-nav > li > a {
  color: white;
}

.navbar-default .navbar-brand {
  color: white;
}

.navbar-default .navbar-toggle {
  border-color: white;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: white;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: transparent;
}

.navbar-default .navbar-brand:focus, .navbar-default .navbar-brand:hover {
  color: white;
  background-color: transparent;
}

.navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover {
  color: white;
  background-color: transparent;
}
